footer {
    background-color:$offblack;
}

footer .container {

    display:flex;
    flex-direction: column;
    align-items:center;
    justify-items: center;
    text-align: center;

    padding:4rem;
    font-weight:300;
    font-size:0.9rem;

    margin-top:0;
    margin-bottom:0;

    @include breakpoint-up(large){        
        flex-direction: row;  
        
    }

    >*{
        margin-top:1rem;
        margin-bottom:1rem;

        @include breakpoint-up(large){        
            flex-grow:1; 
            margin-top:0rem;
            margin-bottom:0rem;           
        }
        
    }

	svg {
		max-height:24px;
		max-width:24px;
		margin-right:0.5rem;
		fill:$white;	
	}

    p>span {
        display:block;
    }

    strong {
        font-weight:700;
    }

    .col-1 {
        >svg {
            max-height:3rem;
        }        
    }

    .col-2 {
        @include breakpoint-up(large){       
            text-align:left;
        }
    }

    .social a:hover svg {
        fill:$dsblue;
    }
    
    .social svg{
        width:24px;
        transition-timing-function: ease-in-out;
        transition-duration: 0.15s;
        //opacity:0.5;
    }

    .social {
        margin-bottom:1rem;
    }

    .contact {
        display:flex;
        flex-direction: column;
        justify-items:center;
        row-gap: 1rem;
    }

    .material-symbols-outlined {
        margin-right:10px;
        transform:scale(0.75);

    }

    .contact>* {
        display:flex;
        flex-direction: row;
        align-items:center;
        margin:0 auto;
    }

    .email, a {
        color:#fff;
        transition-timing-function: ease-in-out;
        transition-duration: 0.15s;
        &:hover {
            color:$dsblue;
            cursor:pointer;
        }
    }

    .mobile {
        cursor:pointer;
    }

  
}
