code {
	box-sizing: border-box;
	display: block;
	letter-spacing: 0;
	padding: 2rem;
	width: 100%;
	margin: 0;
}

pre {
	flex: 0 0 100%;
	overflow: auto;
	margin: 0;
	padding: 0;
	white-space: pre-wrap;
}

.full-screen {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	transform: translateY(0px);
	background-color:rgba($color: #000000, $alpha: 0);
	pointer-events: none;
}