html {
    font-size:100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    font-family: "Lato", Arial, Helvetica, sans-serif;
    line-height: 1.4;
    //letter-spacing: 0.1rem;
    font-weight: 400;
    margin:0; 
    padding:0;
    background-color:$darkgrey;
    color: $lightgrey;
    //overflow:hidden;
}

section:nth-child(odd){
	background-color:$darkergrey;
}

a, a:hover, a:visited, a:active {
    text-decoration: none;
    color: $white;
    cursor: pointer; 
    transition-timing-function: ease-in-out;
    transition-duration: 0.1s;
}

a:hover {

    color:$dsblue;
    cursor:pointer;
}

ul {

	list-style-type: square;
    padding-inline-start: 0px;
	margin:0;

	li {
		margin-top:0;
		margin-bottom: 2rem;

		&:last-child {
			margin-bottom:0;
		}

	}

	li h2 {
		margin-bottom: 0;
	}

}

.container {
	max-width: 1400px;
    margin: 0 auto; 
    padding:2rem;
	
}

.full-height {
    min-height:100vh;
    align-items:center;
}

.hide-for-mobile {
    @include breakpoint-down(medium){
        display:none;
    }
}

.hide-for-desktop {
    @include breakpoint-up(large){
        display:none;
    }
}

hr {
	width:20%;
	height:8px;
	margin-top:4rem;
	margin-bottom:4rem;
	border:0;
	border-bottom:8px solid $midgrey;
}

.mb-1 {
	margin-bottom:1rem;
}

.bg-dark {
    background-color:$darkergrey;
}

p {
    margin-top:0;
}

video {
    max-width:100%;
    margin:0 auto;
}

img {
    max-width:100%;
}

.sm-h {
    height:16rem;
}

.video-wrap {
    display:flex;
    max-height:100%;
}

.center-content {
    display:flex;
    justify-items: center;
    margin:0 auto;
}

.no-pad{
	padding:0;
}

.pad-y-2 {
	padding-top:2rem;
	padding-bottom:2rem;
}

.pad-y-6 {
	padding-top:6rem;
	padding-bottom:6rem;
}

.no-transform {
	text-transform: none;
}

.icons {
	display:none;
}