}

.tab-group {

	.tab-group-content {
		background: $darkergrey;
		padding: 3rem;

		.hidden {
			display: none;
		}
	}

	.tab-group-tabs {
		display: flex;
		gap: 1rem;

		//margin-bottom:2rem;
		div {
			float: left;
			display: block;
			color: $midgrey;
			background-color: $darkergrey;
			padding: 1rem;
			font-weight: 800;

			&.active {
				color: $white;
				background-color: $darkergrey;

			}
		}
	}
}

.wide-container {
	padding-left: 5rem;
	padding-right: 5rem;
}

.rule-sheet {
	font-size: 1.2rem;
	min-height: 100vh;
	width: 100%;
	//border: 1px solid red;
	align-items: center;
	display: flex;
	padding: 6rem;

	>* {
		flex-grow: 1;
	}

	h1 {
		color: $white;
		font-size: 5rem;
		margin-top: 3rem;
		margin-bottom: 1.5rem;

		&:first-of-type {
			margin-top: 0;
		}
	}

	h2 {
		font-size: 1.5rem;
		margin-bottom: 0rem;
	}

	.quote {
		align-self: flex-end;
		justify-self: right;
		padding: 2rem;
		padding-left: 6rem;
		font-size: 1.5rem;
		font-weight: 400;
		flex-basis: 25%;
		color: $midgrey;
		border: 1rem solid $darkgrey;
		position: relative;

		&:before {
			content: '’’';
			position: absolute;
			left: 0em;
			top: -0.25em;
			padding: 0;
			margin: 0;
			pointer-events: none;
			font-size: 12em;
			text-align: left;
			font-family: Arial;
			letter-spacing: -1.0rem;
			font-style: normal;
			font-weight: 400;
			color: $darkgrey;
		}
	}

}

.general-rules {
	font-size: 1.5rem;

	li {
		margin-bottom: 1rem;
		list-style-type: none;
	}
}


.principal {

	padding: 1.5rem;
	background: $darkergrey;
	margin-bottom: 1rem;
	background-color: rgba($color: #000000, $alpha: 0.2);
	border-radius: 0.2rem;
	box-shadow: 0 0 5px 5px rgba($color: #000000, $alpha: 0.2);

	.title {
		display: block;
		color: $white;
		font-size: 4rem;
		margin-bottom: 0.5rem;
	}

	.subtitle {}
}

.project-subfolders {
	h1 {
		letter-spacing: 0.1rem;
		text-transform: none;
		;
	}
}

