$icon-size: 32px;
$icon-thickness: 6px;
$border-size: $icon-size - $icon-thickness;

.hidden {
   opacity: 0;
   transition-duration: 0.5s;
   visibility: hidden;
}


.loading {
   width:100%;
   height:100vh;
   position:fixed; 
   z-index:100;
   background-color:$darkgrey;

   display:flex;
   align-items:center;
   justify-items:center;
   text-align:center;

   svg {
      position:absolute;
      top:50%;
      left:50%;
      transform:translate(-50%, -50%) scale(0.5);
    }
    
    .line-cornered {
    }
    
    $color-still: rgb(35,35,35);
    
    .stroke-still {
       animation: stoke-still-color 0.25s linear;
       animation-delay:0.25s;
       animation-fill-mode: forwards;
      
    }
    
    .stroke-animation {
      animation: stroke-spacing 1s linear, stroke-color 2s linear;
      animation-iteration-count: 1;
      animation-delay: 1s;
      animation-direction: normal;
      animation-fill-mode: forwards;
      animation-play-state:running;
      transform-origin: center center;
    }
    
    @keyframes stroke-spacing {
      0% {
         stroke-dashoffset: 0;
         stroke-dasharray: 0 400;
      }
      100%  {
         stroke-dashoffset: 0;
         stroke-dasharray: 400 400;
      }
    }

    @keyframes stoke-still-color {
       0% {
          stroke:$darkgrey;
       }
       100% {
          stroke: rgb(35,35,35);
       }
    }
    
    $color-1: $white;
    $color-2: $darkgrey;
    
    @keyframes stroke-color {
      0% {
        stroke: $color-1;
      }     
      25% {
        stroke: $color-1;
      } 
      75% {
         stroke: $color-1;
       }   

      100% {
         stroke: $color-2;
      }
    }
}

